import styled from "styled-components";
import Modal from './Modal';
import { useState } from "react";

type SectionCardProps = {
    image: string;
    title: string;
    detail: string;
};

const SectionCard = (props: {cardKey: number, image: string, title: string, detail: string, isModal: (isShow: any) => void, setKey: (key: any) => void }): JSX.Element => {

    function setState(){
        props.isModal(true)
        props.setKey(props.cardKey)
    }

    return (<>
        <_SectionCard onClick={() => {setState()}}>
            <_Recordlist>
                <_Image src={props.image} alt="image"></_Image>
            </_Recordlist>
            <_Recordlist2>
                <_Catch>
                    {props.title}
                </_Catch>
                <_Detail>
                    {props.detail}
                </_Detail>
            </_Recordlist2>
        </_SectionCard>
    </>
    );
};

const _SectionCard = styled.div`
    display: inline-block;
    width: 400px;
    height: 200px;
    margin-top: 20px;
    background-Color: rgb(182, 216, 154);
    border: solid;
    margin-bottom: 20px;
    margin-right: 20px;
    text-align: left;
    &:hover{
        background-Color: rgba(0,0,0,0.5);
    }
`;

const _Recordlist = styled.div`
    display: inline-block;
    margin: 5px;
    padding: 10px;
    box-sizing: border-box;
    vertical-align: top;
    position: relative;
    text-align: left;
`;

const _Recordlist2 = styled.div`
    display: inline-block;
    width: 100px;
    height: 200px;
    margin: 10px;
    padding: 10px;
    box-sizing: border-box;
    vertical-align: top;
    position: relative;
    text-align: left;
`;

const _Catch = styled.div`
    display: inline-block;
    width: 180px;
    font-size: 15px;
    color: rgba(0, 0, 0, 0.719);
    margin-bottom: 20px;
    border-bottom: 2px solid black;
    font-weight: bold;
`;

const _Detail = styled.div`
    display: inline-block;
    width: 170px;
    font-size: 12px;
    color: rgb(0, 0, 0);
`;

const _Image = styled.img`
    width: 165px;
    height: 150px;
    border-radius: 5px;
    object-fit: contain;
`;

export default SectionCard;
