import styled from "styled-components";
import Modal from './Modal';
import SectionCard from './SectionCard';
import melody from './img/melody.png';
import quantize from './img/quantize.png';
import instrument from './img/instrument.png';
import copy from './img/copy.png';
import { useState } from "react";

export enum Manuacategory {
    MELODY,
    HARMONAIZE,
    OTHER
}

const melodyitems = [
    {
        title: `01.画面の見方`,
        detail: `画面の各項目についての説明`,
        image: melody,
    },
    {
        title: `02.クオンタイズについて`,
        detail: `ノーツの入力する幅`,
        image: quantize,
    },
    {
        title: `03.メロディを入力`,
        detail: `ノーツの入力方法について`,
        image: melody,
    },
    {
        title: `04.ノーツの音程を変える`,
        detail: ``,
        image: melody,
    },
    {
        title: `05.ノーツの幅を変える`,
        detail: ``,
        image: melody,
    },
    {
        title: `06.ノーツを移動する`,
        detail: ``,
        image: melody,
    },
    {
        title: `07.ノーツの複数選択`,
        detail: ``,
        image: melody,
    },
    {
        title: `08.メロディを再生`,
        detail: ``,
        image: melody,
    },
    {
        title: `09.フレーズをコピー`,
        detail: ``,
        image: copy,
    },
    {
        title: `10.キーボード操作`,
        detail: ``,
        image: melody,
    },
];

const harmonaizeitems = [
    {
        title: `MMM`,
        detail: `MMM`,
        image: melody,
    },
];

const Manual = (): JSX.Element => {

    const [show, setShow] = useState(false);
    const [num, setNum] = useState(-99);

    const melodyJSXList: JSX.Element[] = [];
    melodyitems.forEach((record, i) => {
        melodyJSXList.push(
            <SectionCard
                cardKey={i}
                image={record.image}
                title={record.title}
                detail={record.detail}
                isModal={(_isShow) => {
                    setShow(_isShow)
                }}
                setKey={(_key) => {
                    setNum(_key)
                }}
            />
        )
    });

    const harmonaizeJSXList: JSX.Element[] = [];
    harmonaizeitems.forEach((record, i) => {
        harmonaizeJSXList.push(
            <SectionCard
                cardKey={9}
                image={record.image}
                title={record.title}
                detail={record.detail}
                isModal={(_isShow) => {
                    setShow(_isShow)
                }}
                setKey={(_key) => {
                    setNum(_key)
                }}
            />
        )
    });

    return (<>
        <_Record>
            <_Title>メロディ</_Title>
            <_Cardlist>
                {melodyJSXList}
            </_Cardlist>
            <_Title>ハーモナイズ</_Title>
            <_Cardlist>
                {harmonaizeJSXList}
            </_Cardlist>
            <Modal isModal={(_isShow) => {
                setShow(_isShow)
            }}
                show={show}
                manualKey={num}></Modal>
        </_Record>
    </>
    );
};

const _Record = styled.div`
    display: inline-block;
    width: 100%;
    height: 100%;
    margin-top: 20px;
    background-Color: rgb(141, 209, 86);
`;

const _Title = styled.div`
    font-size: 38px;
    color: rgba(15, 7, 7, 0.719);
    font-weight: 500;
    margin-bottom: 20px;
`;

const _Cardlist = styled.div`
    display: inline-block;
    width: 1000px;
    height: 100%;
    margin-right: auto;
    margin-left: auto;
`;

export default Manual;
