import styled from "styled-components";
import manual1 from './img/manual1.png';
import manual2 from './img/qontaizedetail.png';
import Manual from "./Manual";

const manualDetail = [
    {
        image: manual1,
        detail:`　①ノーツ
        　　音程と音を鳴らす長さを表したもの。
        　　主音、音階の構成音の場合は赤、非構成音の場合は黄色になります。
        　　ノーツ入力欄の背景色が白の音は主音、薄いグレーは音階の構成音、
        　　濃いグレーは非構成音を表しています。
        　②音程
        　　音の高さが表示されています。
        　　A～G#が音程、hihiやhiがオクターブを表しています。
        　③タイムライン
        　　音を鳴らすタイミングや長さの基準となる時間が表示されています。
        　　メモリの上の１や２は小節を表しています。
        　④楽器一覧
        　　どの楽器でメロディを鳴らすかを選択できます。
        　⑤クオンタイズ
        　　ノーツを扱う際の基準となる幅を変更できます。`
    },
    {
        image: manual2,
        detail:`ノーツを扱う際の入力する幅を変更することが出来ます。

        ①１６ボタン
        　１６分の幅でノーツの入力することが出来ます。
        ②８ボタン
        　８分の幅でノールの入力をすることが出来ます。
        ③４ボタン
        　４分の幅でノーツの入力をすることが出来ます。
        ④/３ボタン
        　選択中の幅の１/３の幅でノーツを入力することが出来ます。
        　/３ボタンは８、４分の場合、選択することが出来ます。`
    },
    {
        image: ``,
        detail:`ノーツの入力は鳴らしたい音の初めから終わりまでをマウスでドラッグすることで入力できます。`
    },
    {
        image: ``,
        detail:`音程を変更したいノーツをフォーカスした状態で、
        画面上部のUP、DOWNボタンを押下することで音程を１ずつ上げ下げ出来ます。`
    },
    {
        image: ``,
        detail:`aaaaaaa`
    },
    {
        image: ``,
        detail:`aaaaaaa`
    },
    {
        image: ``,
        detail:`aaaaaaa`
    },
    {
        image: ``,
        detail:`aaaaaaa`
    },
    {
        image: ``,
        detail:`aaaaaaa`
    },
    {
        image: ``,
        detail:`aaaaaaa`
    },
];

const Modal = (props: { isModal: (isShow: any) => void, show: any, manualKey: any }): JSX.Element => {

    const showModal = (): JSX.Element => {
        if (props.show) {
            return (
                <_Overlay>
                    <_Content>
                        <_Image src={manualDetail[props.manualKey].image}></_Image>
                        <_Detail>{manualDetail[props.manualKey].detail}</_Detail>
                    </_Content>
                    <_Closebutton onClick={() => { props.isModal(false) }}></_Closebutton>
                </_Overlay>
            )
        } else {
            return (<></>);
        }
    };

    return (<>
        {showModal()}
    </>
    );
};

const _Overlay = styled.div`
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background-color:rgba(0,0,0,0.5);
    display: flex;
    align-items: center;
    justify-content: center;
`;

const _Content = styled.div`
    overflow-y: auto;
    overflow-y: scroll;
    z-index:3;
    height:60%;
    padding: 1em;
    background:#fff;
`;

const _Image = styled.img`
    width: 600px;
    height: 400px;
    border-radius: 5px;
    margin-left: 25px;
    margin-right: 25px;
    margin-top: 50px;
    object-fit: contain;
    z-index:3;
`;

const _Detail = styled.div`
    display: inline-block;
    font-family: serif;
    width: 600px;
    font-size: 16px;
    line-height: 28px;
    color: rgb(0, 0, 0);
    white-space: pre-line;
    text-align: left;
    vertical-align: top;
    margin-top: 50px;
`;

const _Closebutton = styled.button`
  display: block;
  position: absolute;
  top:110px;
  width: 30px;
  height: 30px;
  border: 2px solid rgb(255, 255, 255); /* 枠の調整 */
  border-radius: 50%;  /* 丸みの度合い */
  background: rgba(255,255,255,0);
  &:after{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 3px; /* 棒の幅（太さ） */
    height: 22px; /* 棒の高さ */
    background: rgb(255, 255, 255); /* バツ印の色 */
    transform: translate(-50%,-50%) rotate(-45deg);
  }
  &:before{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 3px; /* 棒の幅（太さ） */
    height: 22px; /* 棒の高さ */
    background: rgb(255, 255, 255); /* バツ印の色 */
    transform: translate(-50%,-50%) rotate(45deg);
  }
`;


export default Modal;
